.navbar {
	color: white;
	background-color: $brand-primary;
	font-family: sans-serif;
	font-weight: bold;
	padding: 5px;

	a, a:hover {
		color: white;
	}

	.navbar-brand {
		font-weight: bold;
		img {
			height: 20px;
		}
	}
	
	.navbar-nav {
		> li {
	
			> a {
				padding: .25rem .5rem;
				color: white;
				background-color: $brand-primary;
				border-radius: 5px;

				&:hover {
					color: $brand-primary;
					background-color: white;	
				}

				&.active, &.active:hover {
					color: $brand-primary;
					background-color: white;
				}
			}
		}

		//
		// pill spacing
		//

		> li + li {
			margin-left: 2px;
		}
	}

	.navbar-toggler {
		padding: 5px;
		color: grey;
		background: white;
		outline: none;

		.navbar-toggler-icon {
			height: auto;
		}
	}

	//
	// collapsed nav item spacing
	//

	.navbar-collapse.show {
		.navbar-nav {
			> li {
				margin-top: 5px;
			}
		}
	}
}